a.item {
    border-style: solid;
    border-width: 3px;
    border-radius: 5px;
    border-color: #82B78A;
    float: left;
    padding: 10px 0px;
    margin: 30px 20px;
    background-color: #C9C0AC;
}
a.item:hover {
    transform: translateY(-10px);
  }

.image {
    height: 200px;
    width: 200px;
    padding: 6px 15px;
  }
  